@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.selectToken {
  width: 148px;
  height: 36px;
  background: $mainGreen;
  box-shadow: 0px 2px 26px rgba(14, 14, 15, 0.06);
  border-radius: 15px;
  cursor: pointer;
  @extend %flexCenter;
  transition: all 0.2s ease;

  &:hover {
    background-color: lighten($mainGreen, 3%);
  }
  &:active {
    background-color: darken($mainGreen, 3%);
  }
  p {
    font-weight: 600;
    font-size: 16px;
    color: $white;
    line-height: 20px;
    margin-right: 6px;
  }
  &__arrow {
    transform: rotate(180deg);
    svg path {
      fill: $white;
    }
  }
}

.selectedToken {
  position: relative;
  width: 105px;
  height: 36px;
  background-color: var(--backgroud);
  border-radius: 12px;
  padding: 6px 12px;
  cursor: pointer;
  @extend %shadowLayout;
  @extend %alignCenter;

  &__name {
    @extend %alignCenter;
    img {
      width: 24px;
      height: 24px;
    }
    p {
      font-weight: 600;
      font-size: 16px;
      color: var(--logoColor);
      text-transform: uppercase;
      margin-left: 6px;
    }
  }
  &__arrow {
    position: absolute;
    right: 6px;
    top: 50%;
    transform: translateY(-50%) rotate(180deg);
    svg {
      path {
        fill: var(--logoColor);
      }
    }
  }
}

@media (max-width: 600px) {
  .selectToken {
    width: 132px;
    height: 32px;
    p {
      font-size: 14px;
      line-height: 18px;
    }
    &__name {
      img {
        width: 20px;
        height: 20px;
      }
    }
  }
}
