@import "../../../scss/repeat.scss";
@import "../../../scss/vars.scss";

.transactions {
  width: 100%;
  @extend %block;
  padding: 20px 0 24px;

  &__title {
    padding: 0 24px;
    margin-bottom: 20px;
    @extend %alignCenter;
    font-weight: 600;
    font-size: 20px;
    color: var(--logoColor);
    p {
      font-size: 16px;
      color: var(--menuColor);
      margin-left: 10px;
    }
  }

  &__table {
    table {
      @extend %table;
      thead {
        tr {
          @extend %trHead;
        }
      }
      tbody {
        tr {
          height: 60px;
          @extend %trBody;
          transition: background-color 0.2s ease;
          &:hover {
            background-color: var(--hover);
          }
        }
      }
      tr {
        @extend %borderTable;
      }
      td {
        min-width: 100px;
        padding: 17px 24px;
      }
    }
  }
}
