@import "../../../scss/repeat.scss";
@import "../../../scss/vars.scss";

.selectTokenModal {
  display: flex;
  flex-direction: column;
  width: 488px;
  height: 572px;
  @extend %block;
  padding: 20px 24px 0;

  &__search {
    display: inline-block;
    background-color: transparent;
    width: 100%;
    height: 48px;
    padding: 14px 20px;
    margin: 20px 0;
    @extend %input;
    border-radius: 12px;
    border: 1px solid $mainGreen;
    font-size: 16px;
    color: var(--logoColor);
    line-height: 20px;
    &::placeholder {
      color: var(--menuColor);
    }
  }
  &__list {
    flex-grow: 1;
    overflow-y: auto;
    &::-webkit-scrollbar {
      display: none;
    }
  }
  &__item {
    cursor: pointer;
    transition: all 0.2s ease;
    &:hover {
      opacity: 0.4;
    }
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }
  &__manage {
    width: 100%;
    min-height: 64px;
    @extend %flexCenter;
    cursor: pointer;
    font-weight: 600;
    font-size: 16px;
    color: $mainGreen;
    svg {
      margin-right: 10px;
    }
  }
}

@media (max-width: 600px) {
  .selectTokenModal {
    width: 95%;
    margin: 0 auto;
    &__search {
      font-size: 16px;
      line-height: 20px;
    }
  }
}
