@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

%text {
  font-weight: 600;
  font-size: 14px;
  color: var(--selectColor);
}

.selector {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: var(--backgroud);
  border: 1px solid var(--borderColor);
  border-radius: 8px;
  &__inner {
    width: 100%;
    height: 100%;
    padding: 0 30px 0 16px;
    cursor: pointer;
    @extend %alignCenter;
    @extend %text;
    svg {
      position: absolute;
      right: 12px;
      top: 50%;
      transform: translateY(-50%) rotate(180deg);
      transition: all 0.2s ease;
      path {
        fill: var(--logoColor);
      }
    }
    &.active > svg {
      transform: translateY(-50%) rotate(0deg);
    }
    &.active ~ .selector__list {
      max-height: 200px;
      border: 1px solid var(--borderColor);
    }
  }
  &__list {
    position: absolute;
    left: 0;
    right: 0;

    z-index: 5;
    width: 100%;
    max-height: 0;
    overflow: auto;
    background-color: var(--backgroud);
    border: 0px solid transparent;
    border-radius: 8px;
    transition: all 0.3s ease;

    &.upStyle {
      top: 0;
      transform: translateY(calc(-100% - 5px));
    }
    &.downStyle {
      bottom: 0;
      transform: translateY(calc(100% + 5px));
    }
    &::-webkit-scrollbar {
      display: none;
    }
  }
  &__item {
    @extend %text;
    padding: 10px;
    cursor: pointer;
    transition: all 0.2s ease;
    &:hover {
      background-color: var(--borderColor);
    }
  }
}
