%button {
  display: inline-block;
  outline: none;
  border: none;
  cursor: pointer;
}

%input {
  outline: none;
  border: none;
}

%center {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

%shadow {
  box-shadow: 0px 2px 14px rgba(14, 14, 15, 0.04);
}
%shadowHover {
  box-shadow: 0px 2px 14px rgba(14, 14, 15, 0.18);
}
%shadowLayout {
  box-shadow: 0px 2px 40px rgba(14, 14, 15, 0.07);
}

%alignCenter {
  display: flex;
  align-items: center;
}

%flexColumn {
  display: flex;
  flex-direction: column;
}

%flexCenter {
  display: flex;
  align-items: center;
  justify-content: center;
}

%block {
  background-color: var(--backgroud);
  border-radius: 15px;
  @extend %shadowLayout;
}

%table {
  width: 100%;
  border-collapse: collapse;
}
%borderTable {
  border-bottom: 1px solid var(--borderColor);
}
%trHead {
  font-size: 14px;
  color: var(--menuColor);
  line-height: 18px;
  cursor: pointer;
}
%trBody {
  font-size: 16px;
  color: var(--logoColor);
  line-height: 20px;
}
