@import "../../../../scss/repeat.scss";
@import "../../../../scss/vars.scss";

.swap {
  width: 490px;
  margin: 30px auto 0;
  &__item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__calculator {
    &Info {
      @extend %alignCenter;
      justify-content: flex-end;
      font-size: 16px;
      color: var(--logoColor);
      line-height: 20px;
      text-align: right;
      margin-top: 14px;
      svg {
        margin-left: 5px;
      }
    }
    &Arrow {
      margin: 8px 0;
      text-align: center;
    }
    &Btn {
      width: 100%;
      height: 52px;
      margin-top: 14px;
    }
  }

  &__info {
    @extend %block;
    padding: 20px 24px;
  }
}

@media (max-width: 600px) {
  .swap {
    width: 100%;
    margin: 0;
    &__item {
      &:not(:last-child) {
        margin-bottom: 12px;
      }
    }
    &__calculatorBtn {
      width: 100%;
      height: 48px;
    }
  }
}
