@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.inputCrypto {
  width: 100%;
  height: 80px;
  background-color: var(--hover);
  border: 1px solid var(--borderColor);
  border-radius: 15px;
  padding: 14px 24px;
  @extend %alignCenter;
  justify-content: space-between;

  &__label {
    font-weight: 600;
    font-size: 14px;
    color: var(--selectColor);
    line-height: 18px;
    margin-bottom: 6px;
  }
  &__inner {
    input {
      width: 100px;
      @extend %input;
      background-color: transparent;
      font-weight: 600;
      font-size: 24px;
      color: var(--logoColor);
      line-height: 28px;
      &::placeholder {
        color: var(--menuColor);
        opacity: 0.6;
      }
    }
  }
}

@media (max-width: 600px) {
  .inputCrypto {
    padding: 10px 15px;
    &__label {
      font-size: 12px;
      line-height: 16px;
    }
    &__inner input {
      font-size: 20px;
      line-height: 24px;
    }
  }
}
