@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.chooser {
  @extend %alignCenter;
  &__item {
    min-width: 40px;
    height: 32px;
    @extend %flexCenter;
    padding: 0 8px;
    border-radius: 8px;
    font-weight: 600;
    font-size: 14px;
    color: var(--menuColor);
    line-height: 18px;
    cursor: pointer;
    &:not(:first-child) {
      margin-left: 4px;
    }

    &.active {
      background-color: $skyMainGreen;
      color: $mainGreen;
    }
  }
}

@media (max-width: 600px) {
  .chooser {
    &__item {
      min-width: 31px;
      height: 24px;
      font-size: 12px;
      line-height: 16px;
    }
  }
}
