@import "../../../scss/repeat.scss";
@import "../../../scss/vars.scss";

.pool {
  &__header {
    @extend %alignCenter;
    justify-content: space-between;
    &Box {
      @extend %alignCenter;
    }
  }
  &__swap {
    width: 108px;
    height: 52px;
    margin-right: 10px;
  }
  &__add {
    width: 169px;
    height: 52px;
  }
  &__cash {
    @extend %alignCenter;
    &Icon {
      width: 56px;
      height: 56px;
    }
    &Name {
      margin-left: 16px;
      p {
        font-weight: 600;
        font-size: 32px;
        color: var(--logoColor);
        line-height: 36px;
        & > label {
          font-weight: 600;
          font-size: 32px;
          color: var(--logoColor);
          line-height: 36px;
          color: var(--menuColor);
        }
      }
      label {
        font-size: 18px;
        color: var(--menuColor);
        line-height: 24px;
      }
    }
  }
  &__line {
    width: 1px;
    height: 62px;
    background-color: var(--svgColorMenu);
    margin: 0 40px;
  }
  &__price {
    font-weight: 600;
    font-size: 40px;
    color: var(--logoColor);
    line-height: 36px;
  }

  &__box {
    display: flex;
    margin: 40px 0 20px;
  }
  &__left {
    width: 515px;
    margin: 0 20px -20px 0;
    @extend %alignCenter;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__infoItem {
    .info__item {
      height: 92px;
    }
    margin-bottom: 20px;
  }
  &__right {
    width: calc(100% - 535px);
    @extend %block;
    padding: 18px 24px 24px;
  }
  &__chartHeader {
    @extend %alignCenter;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  &__chooseChart {
    @extend %alignCenter;
    &Item {
      font-weight: 600;
      font-size: 20px;
      color: var(--menuColor);
      line-height: 24px;
      cursor: pointer;
      &.active {
        color: var(--logoColor);
      }
      &:not(:last-child) {
        margin-right: 20px;
      }
    }
  }
  &__chart {
    width: 100%;
    height: 336px;
  }
}
