@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.modalHeader {
  @extend %alignCenter;
  justify-content: space-between;
  &__title {
    font-weight: 600;
    font-size: 20px;
    color: var(--logoColor);
  }
  &__close {
    cursor: pointer;
    svg path {
      fill: var(--logoColor);
    }
  }
}

@media (max-width: 600px) {
  .modalHeader {
    &__title {
      font-size: 18px;
      line-height: 22px;
    }
  }
}
