@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.poolItem {
  padding: 12px 16px;
  &:not(:last-child) {
    border-bottom: 1px solid var(--borderColor);
  }

  &__upBox {
    @extend %alignCenter;
    justify-content: space-between;
  }
  &__price {
    font-size: 14px;
    color: var(--logoColor);
    line-height: 18px;
  }
  &__btns {
    @extend %alignCenter;
  }
  &__btn {
    width: 30px;
    height: 30px;
    &:not(:last-child) {
      margin-right: 6px;
    }
    .iconButton {
      font-size: 0;
      img {
        width: 13px;
        height: 13px;
        margin-right: 0;
      }
    }
  }

  &__downBox {
    margin-top: 6px;
    @extend %alignCenter;
    justify-content: space-between;
  }

  &__case {
    h5 {
      font-weight: 400;
      font-size: 12px;
      color: var(--menuColor);
      line-height: 16px;
    }
    h3 {
      font-weight: 400;
      font-size: 14px;
      color: var(--logoColor);
      line-height: 18px;
    }
  }
}
