@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.button {
  width: 100%;
  height: 100%;
  background-color: $mainGreen;
  @extend %button;
  @extend %flexCenter;
  border-radius: 12px;
  font-weight: 600;
  font-size: 18px;
  color: $white;
  transition: all 0.2s ease;

  &:hover {
    background-color: lighten($mainGreen, 3%);
  }
  &:active {
    background-color: darken($mainGreen, 3%);
  }
}

@media (max-width: 600px) {
  .button {
    font-size: 16px;
    line-height: 20px;
  }
}
