@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.statisticsHeader {
  @extend %alignCenter;
  justify-content: space-between;

  &__left {
    @extend %alignCenter;
  }
  &__title {
    font-weight: 600;
    font-size: 20px;
    color: var(--logoColor);
    line-height: 24px;
    margin-right: 20px;
  }
  &__selector {
    width: 145px;
    height: 36px;
    &.mobile {
      display: none;
    }
  }
}

@media (max-width: 600px) {
  .statisticsHeader {
    &__title {
      font-size: 18px;
      line-height: 22px;
    }
  }
}

@media (max-width: 450px) {
  .statisticsHeader {
    flex-wrap: wrap;
    &__selector {
      width: 100%;
      &.lap {
        display: none;
      }
      &.mobile {
        display: block;
        margin-top: 14px;
      }
    }
  }
}
