@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

%title {
  font-weight: 600;
  font-size: 16px;
  color: var(--menuColor);
}

.navList {
  position: relative;
  width: 100%;
  height: 48px;
  @extend %flexCenter;
  margin-top: 16px;

  &.active {
    .navList__inner {
      background-color: $skyMainGreen;

      p {
        color: var(--logoColor);
      }
      svg:first-child path {
        fill: $mainGreen;
        stroke: $mainGreen;
      }
      svg:last-child {
        transform: translateY(-50%) rotate(0deg);
      }
    }
    ~ .navList__box {
      max-height: 400px;
    }
  }

  &__inner {
    width: 238px;
    height: 100%;
    @extend %alignCenter;
    padding: 0 25px 0 12px;
    border-radius: 8px;
    svg:first-child {
      width: 24px;
      height: 24px;
      path {
        fill: var(--svgColorMenu);
        stroke: var(--svgColorMenu);
      }
    }
    svg:last-child {
      position: absolute;
      right: 32px;
      top: 50%;
      transform: translateY(-50%) rotate(180deg);
      transition: all 0.2s ease-in-out;
      path {
        fill: var(--logoColor);
      }
    }

    p {
      @extend %title;
      margin-left: 12px;
    }
  }

  &__box {
    max-height: 0;
    overflow: hidden;
    transition: all 0.4s ease-in-out;
  }
  &__item {
    position: relative;
    width: 100%;
    height: 48px;
    @extend %alignCenter;
    cursor: pointer;
    @extend %title;
    padding-left: 63px;
    &.active {
      color: var(--logoColor);

      &::after {
        content: "";
        position: absolute;
        right: 0;
        height: 100%;
        width: 3px;
        background-color: $mainGreen;
        border-radius: 10px 0 0 10px;
      }
    }
  }
}
