@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.resultItem {
  @extend %alignCenter;
  justify-content: space-between;
  &:not(:last-child) {
    margin-bottom: 6px;
  }
  &__title {
    font-weight: 600;
    font-size: 14px;
    color: var(--menuColor);
    @extend %alignCenter;
    svg {
      margin-left: 5px;
      path {
        fill: var(--menuColor);
      }
    }
  }
  &__value {
    font-weight: 600;
    font-size: 14px;
    color: var(--logoColor);
  }
}
