@import "../../../scss/vars.scss";
@import "../../../scss/repeat.scss";
.header {
  position: sticky;
  top: 0;
  z-index: 5;
  width: 100%;
  height: 70px;
  @extend %alignCenter;
  justify-content: space-between;
  background-color: var(--backgroud);
  border-bottom: 1px solid var(--borderColor);
  padding: 0 30px;

  &__titleTablet {
    display: none;
  }
  &__burger {
    margin-right: 27px;
    cursor: pointer;
    svg path {
      fill: var(--logoColor);
    }
  }
  &__title {
    @extend %alignCenter;
    font-weight: 600;
    font-size: 20px;
    color: var(--logoColor);
    text-transform: capitalize;
  }
  &__back {
    width: 24px;
    height: 24px;
    cursor: pointer;
    margin-right: 20px;
    svg {
      transform: rotate(90deg);
      path {
        fill: var(--logoColor);
      }
    }
  }
  &__right {
    @extend %alignCenter;
    justify-content: space-between;
  }
  &__notification {
    cursor: pointer;
  }
  &__line {
    width: 1px;
    height: 40px;
    background-color: var(--borderColor);
    margin: 0 24px;
  }
  &__wallet {
    width: 153px;
    height: 40px;
  }
}

@media (max-width: 1100px) {
  .header {
    height: 55px;
    &__titleTablet {
      display: flex;
      align-items: center;
    }
    &__title {
      display: none;
    }
    &__wallet {
      height: 36px;
    }
  }
}

@media (max-width: 600px) {
  .header {
    padding: 0 16px;
    &__line {
      display: none;
    }
    &__wallet {
      margin-left: 24px;
    }
  }
}

@media (max-width: 360px) {
  .header {
    &__wallet {
      margin-left: 10px;
    }
  }
}
