@import "../../../scss/repeat.scss";
@import "../../../scss/vars.scss";

.poolTable {
  background-color: var(--backgroud);
  border-radius: 15px;
  @extend %shadow;
  &__header {
    border-bottom: 1px solid var(--borderColor);
    padding: 20px 24px;
  }
  &__innerMobile {
    display: none;
  }
  &__inner {
    overflow-x: auto;
    table {
      @extend %table;
      thead {
        @extend %borderTable;
        tr {
          @extend %trHead;
          svg {
            vertical-align: middle;
          }
          td {
            white-space: nowrap;
          }
        }
      }
      tbody {
        tr {
          transition: background-color 0.2s ease;
          &:hover {
            background-color: var(--hover);
          }
        }
      }
      tr {
        height: 75px;
        @extend %trBody;
        &:not(:last-child) {
          @extend %borderTable;
        }
      }
      td {
        min-width: 100px;
        padding: 0 24px;
      }

      .tableRowBtn {
        @extend %alignCenter;
        &__left {
          min-width: 100px;
          height: 40px;
          margin-right: 10px;
        }
        &__right {
          min-width: 150px;
          height: 40px;
        }
      }
    }
  }
}

@media (max-width: 850px) {
  .poolTable {
    &__header {
      border-bottom: 1px solid var(--borderColor);
      padding: 20px 16px 12px;
    }
  }
}

@media (max-width: 600px) {
  .poolTable {
    &__inner {
      display: none;
    }
    &__innerMobile {
      display: block;
    }
  }
}

@media (max-width: 360px) {
  .poolTable {
    &__header {
      padding: 20px 8px 12px;
    }
  }
}
