@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.iconButton {
  width: 100%;
  height: 100%;
  @extend %button;
  @extend %flexCenter;
  border-radius: 12px;
  background-color: $mainGreen;
  font-weight: 600;
  font-size: 14px;
  color: $white;
  transition: all 0.2s ease;
  img {
    margin-right: 10px;
  }
  &:hover {
    background-color: lighten($mainGreen, 3%);
  }
  &:active {
    background-color: darken($mainGreen, 3%);
  }

  &.unFill {
    color: $mainGreen;
    background-color: transparent;
    border: 1px solid $mainGreen;
  }

  h3 {
    
    background-color: $mainGreen;
    font-weight: 600;
    font-size: 14px;
    color: $white;
    transition: all 0.2s ease;
  }
}
