@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.cryptoPrice {
  width: 100%;
  height: 76px;
  @extend %block;
  padding: 15px 24px;
  @extend %alignCenter;
  justify-content: space-between;

  &__box {
    @extend %alignCenter;
    margin-right: 10px;
    & > img {
      width: 36px;
      height: 36px;
      margin-right: 12px;
    }
  }
  &__title {
    @extend %alignCenter;
    font-weight: 600;
    font-size: 18px;
    color: var(--logoColor);
    line-height: 24px;
  }
  &__value {
    font-size: 14px;
    color: var(--logoColor);
    line-height: 18px;
  }
  &__chart {
    width: 290px;
    height: 46px;
  }
}

@media (max-width: 600px) {
  .cryptoPrice {
    &__box {
      margin-right: 23px;
    }
    &__title {
      font-size: 16px;
      line-height: 20px;
    }
    &__chart {
      width: 100%;
    }
  }
}

@media (max-width: 600px) {
  .cryptoPrice {
    &__chart {
      width: 260px;
    }
  }
}

@media (max-width: 480px) {
  .cryptoPrice {
    &__chart {
      width: 150px;
    }
  }
}

@media (max-width: 370px) {
  .cryptoPrice {
    height: auto;
    flex-direction: column;
    &__box {
      width: 100%;
      margin-bottom: 15px;
    }
    &__chart {
      width: 100%;
    }
  }
}
