@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.info__item {
  min-width: 248px;
  height: 104px;
  background-color: var(--backgroud);
  padding: 20px 24px 24px;
  border-radius: 15px;
  @extend %shadow;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  &:hover {
    @extend %shadowHover;
    transform: translateY(-4%);
  }
  &:not(:last-child) {
    margin-right: 20px;
  }
  &Up {
    @extend %alignCenter;
    justify-content: space-between;
    svg {
      fill: var(--svgLight);
    }
  }
  &Down {
    margin-top: 8px;
  }
  &Label {
    font-size: 14px;
    color: var(--selectColor);
  }
  &Title {
    @extend %alignCenter;
    font-weight: 600;
    font-size: 28px;
    color: var(--logoColor);
    & > div {
      margin-left: 10px;
    }
  }
}

@media (max-width: 600px) {
  .info__item {
    min-width: 166.5px;
    height: 70px;
    padding: 12px 16px 14px;
    &:not(:last-child) {
      margin-right: 10px;
    }
    &Down {
      margin-top: 4px;
    }
    &Label {
      font-size: 12px;
      line-height: 16px;
    }

    &Title {
      font-size: 20px;
      line-height: 24px;

      & > div {
        margin-left: 5px;
      }
    }
  }
}
