@import "../../../scss/repeat.scss";

.dashboard {
  &__info {
    display: flex;
    overflow-y: scroll;
    padding: 25px 15px;
    margin: -25px -15px;
    &::-webkit-scrollbar {
      display: none;
    }
  }
  &__statistics {
    display: flex;
    margin: 20px -10px 0;
  }
  &__table {
    margin-top: 20px;
    overflow: hidden;
  }
}

@media (max-width: 1400px) {
  .dashboard {
    &__statistics {
      flex-direction: column;
      margin: 20px 0 0;
      .volume {
        margin-bottom: 20px;
      }
    }
  }
}

@media (max-width: 600px) {
  .dashboard {
    &__info {
      padding: 10px 15px;
      margin: -10px -15px;
    }
    &__statistics {
      margin: 10px 0 0;
    }
  }
}
