@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.modal {
  transform: translateY(-150%);
  &.active {
    animation: showModal 0.6s;
    animation-fill-mode: forwards;
  }
  &.unactive {
    animation: hideModal 0.5s;
    animation-fill-mode: forwards;
  }

  &__overlay {
    @extend %flexCenter;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 10;
    background: rgba($logoColor, 0.3);
    opacity: 0;
    visibility: hidden;
    transition: all 0.1s ease;
    &.active {
      animation: overlay 0.1s;
      animation-fill-mode: forwards;
    }
    &.unactive {
      animation: overlayUnactive 0.8s;
      animation-fill-mode: forwards;
    }
  }
}

@keyframes overlay {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}

@keyframes overlayUnactive {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

@keyframes showModal {
  0% {
    transform: translateY(-150%);
  }
  60% {
    transform: translateY(10%);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes hideModal {
  0% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(10%);
  }
  100% {
    transform: translateY(-150%);
  }
}

@media (max-width: 600px) {
  .modal {
    width: 100%;
  }
}
