@import "../../scss/repeat.scss";

.tooltipInner {
  width: 100px;
  height: 62px;
  background-color: var(--tooltip);
  border-radius: 8px;
  box-shadow: 0px 9px 35px rgba(25, 29, 28, 0.08);
  padding: 12px;
  @extend %flexCenter;
  flex-direction: column;
  p {
    font-weight: 600;
    font-size: 16px;
    color: var(--logoColor);
    line-height: 20px;
    margin-bottom: 2px;
  }
  span {
    font-size: 12px;
    color: var(--menuColor);
    line-height: 16px;
  }
}
