@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.cashName {
  @extend %alignCenter;
  img {
    min-width: 36px;
    height: 36px;
  }
  &__inner {
    display: flex;
    flex-direction: column;
    margin-left: 12px;

    p {
      font-weight: 600;
      font-size: 18px;
      color: var(--logoColor);
      line-height: 24px;
    }
    label {
      font-weight: 400;
      font-size: 14px;
      color: var(--menuColor);
      line-height: 18px;
    }
  }
}

@media (max-width: 600px) {
  .cashName {
    img {
      min-width: 28px;
      height: 28px;
    }
    &__inner {
      p {
        font-size: 14px;
        line-height: 18px;
      }
      label {
        font-size: 12px;
        line-height: 16px;
      }
    }
  }
}
