@import "../../scss/vars.scss";

.app {
  display: flex;
  align-items: flex-start;
  &__main {
    width: 100%;
    height: 100vh;
    background-color: var(--backgroudMain);
    overflow-y: auto;
    &Layout {
      padding: 24px 32px;
    }
  }
}

@media (max-width: 1100px) {
  .app {
    &__mainLayout {
      padding: 20px;
    }
  }
}

@media (max-width: 600px) {
  .app {
    &__mainLayout {
      padding: 12px 16px 16px;
    }
  }
}
