@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.search {
  position: relative;
  width: 100%;
  height: 100%;
  input {
    @extend %input;
    width: 100%;
    height: 100%;
    background-color: var(--backgroud);
    border: 1px solid var(--borderColor);
    border-radius: 12px;
    padding: 0 10px 0 60px;

    font-size: 16px;
    color: var(--colorFont);
    line-height: 20px;
    &::placeholder {
      color: var(--menuColor);
    }
  }
  svg {
    width: 24px;
    height: 24px;
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);

    path {
      fill: var(--logoColor);
    }
  }
}

@media (max-width: 600px) {
  .search {
    input {
      font-size: 14px;
      line-height: 18px;
    }
  }
}
