@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.exchangeLayout {
  width: 100%;
  @extend %block;
  padding: 20px 24px 24px;
  &__header {
    @extend %alignCenter;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  &__title {
    font-weight: 600;
    font-size: 20px;
    color: var(--logoColor);
  }
  &__btns {
    @extend %alignCenter;
  }
  &__btn {
    width: 24px;
    height: 24px;
    cursor: pointer;
    &:not(:last-child) {
      margin-right: 20px;
    }
    svg path {
      fill: var(--logoColor);
    }
  }
}

@media (max-width: 600px) {
  .exchangeLayout {
    padding: 16px;
    &__title {
      font-size: 18px;
    }
  }
}
