@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.priceInOut {
  @extend %alignCenter;
  font-size: 16px;
  color: var(--logoColor);
  line-height: 20px;
  &__name {
    img {
      width: 20px;
      height: 20px;
      margin-right: 6px;
    }
    @extend %alignCenter;
    font-weight: 600;
  }
  &__value {
    margin: 0 8px;
  }
}
