@import "../../../scss/repeat.scss";
@import "../../../scss/vars.scss";

.filterTable {
  @extend %alignCenter;
  &__btns {
    @extend %alignCenter;
  }
  &__search {
    margin: 0 40px;
    width: 100%;
    height: 48px;
  }
  &__chooser {
    min-width: 120px;
    height: 48px;
    margin-left: 10px;
    display: none;
  }
}

@media (max-width: 950px) {
  .filterTable {
    &__chooserFull {
      display: none;
    }
    &__chooser {
      display: block;
    }
    &__search {
      margin: 0 0 0 40px;
    }
  }
}

@media (max-width: 850px) {
  .filterTable {
    flex-wrap: wrap;
    &__search {
      width: 100%;
      margin: 12px 0 0;
    }
  }
}

@media (max-width: 600px) {
  .filterTable {
    &__search {
      height: 44px;
    }
    &__chooser {
      min-width: 100px;
      height: 36px;
      margin-left: 16px;
    }
  }
}

@media (max-width: 360px) {
  .filterTable {
    &__chooser {
      width: 100%;
      margin: 12px 0 0;
    }
  }
}
