//colors
$white: #fff;
$black: #000;
$lightGray: #e7ecf0;
$mainGreen: #16c9bd;
$skyMainGreen: rgba(22, 201, 189, 0.1);
$backgroud: #f9fbfd;
$dark: #1b2020;
$logoColor: #17191b;
$svgLight: #cbd5dc;
$svgDark: #50575b;
$lineLight: #e7ecf0;
$backgroudDark: #141616;
$darkGray: #95a5b0;
$darkGray2: #888f95;
$strongGray: #63727d;
$orange: #f7931a;
$borderDark: #313538;
$red: #ea3269;
$skyRed: rgba(234, 50, 105, 0.1);

//fonts
$barlow: "Barlow", sans-serif;

:root {
  --backgroud: #{$white};
  --backgroudMain: #{$backgroud};
  --logoColor: #{$logoColor};
  --menuColor: #{$darkGray};
  --svgColorMenu: #{$svgLight};
  --borderColor: #{$lightGray};
  --themeBtn: #{$darkGray};
  --selectColor: #{$strongGray};
  --colorFont: #{$black};
  --hover: #{$backgroud};
  --tooltip: #{$white};
  --overlay: #{$logoColor};
}

body.darkMode {
  --backgroud: #{$dark};
  --backgroudMain: #{$backgroudDark};
  --logoColor: #{$white};
  --menuColor: #{$darkGray2};
  --svgColorMenu: #{$svgDark};
  --borderColor: #{$borderDark};
  --themeBtn: #{$svgDark};
  --selectColor: #{$darkGray2};
  --colorFont: #{$white};
  --hover: #{$borderDark};
  --tooltip: #{$borderDark};
  --overlay: #{$svgDark};
}
