@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.themeButton {
  position: relative;
  width: 56px;
  height: 32px;
  background-color: var(--themeBtn);
  padding: 5px;
  border-radius: 38px;
  @extend %alignCenter;
  justify-content: space-between;
  cursor: pointer;

  &__circle {
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: $white;
    position: absolute;
    left: 5px;
    top: 50%;
    transform: translateY(-50%);
    transition: all 0.2s ease;

    &.activeDark {
      left: 28px;
    }
  }
}
