@import "../../../scss/vars.scss";
@import "../../../scss/repeat.scss";

.navBar {
  min-width: 270px;
  height: 100vh;
  @extend %flexColumn;
  background-color: var(--backgroud);
  border-right: 1px solid var(--borderColor);
  overflow-y: auto;
  &::-webkit-scrollbar {
    display: none;
  }
  &__overlay {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 8;
    background: rgba($svgDark, 0.3);
    visibility: hidden;
    opacity: 0;
    display: none;
    transition: all 0.2s ease;
    &.active {
      opacity: 1;
      visibility: visible;
    }
  }
  &__close {
    cursor: pointer;
    margin-right: 24px;
    display: none;
    svg path {
      fill: var(--logoColor);
    }
  }
  &__logo {
    min-height: 70px;
    @extend %alignCenter;
    padding: 0 24px;
    border-bottom: 1px solid var(--borderColor);
    img {
      width: 28px;
      height: 28px;
    }
    p {
      font-weight: 700;
      font-size: 18px;
      color: var(--logoColor);
      letter-spacing: 0.06em;
      text-transform: uppercase;
      margin-left: 10px;
    }
  }
  &__menu {
    flex-grow: 1;
    @extend %flexColumn;
    padding: 30px 0;
    border-bottom: 1px solid var(--borderColor);
  }
  &__settings {
    min-height: 145px;
    padding: 25px 25px 30px;
  }
  &__selectors {
    @extend %alignCenter;
    justify-content: space-between;
  }
  &__theme {
    @extend %alignCenter;
    margin-top: 24px;
    p {
      font-weight: 600;
      font-size: 16px;
      color: var(--menuColor);
      margin-left: 16px;
    }
  }
  &__lang {
    width: 125px;
    height: 36px;
  }
  &__currency {
    width: 84px;
    height: 36px;
  }
}

@media (max-width: 1100px) {
  .navBar {
    position: absolute;
    z-index: 10;
    transform: translateX(-100%);
    transition: all 0.3s ease;
    &.active {
      transform: translateX(0);
    }
    &__overlay {
      display: block;
    }
    &__menu {
      padding: 20px 0;
    }
    &__logo {
      min-height: 55px;
      padding: 0 20px;
    }
    &__close {
      display: block;
    }
  }
}
