@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.navItem {
  position: relative;
  width: 100%;
  height: 48px;
  @extend %flexCenter;
  &:not(:first-child) {
    margin-top: 16px;
  }

  &.active::after {
    content: "";
    position: absolute;
    right: 0;
    height: 100%;
    width: 3px;
    background-color: $mainGreen;
    opacity: 0.3;
    border-radius: 10px 0 0 10px;
  }
  &.active {
    .navItem__inner {
      background-color: $skyMainGreen;
      p {
        color: var(--logoColor);
      }
      svg path {
        fill: $mainGreen;
        stroke: $mainGreen;
      }
    }
  }

  &__inner {
    width: 238px;
    height: 100%;
    @extend %alignCenter;
    padding: 0 12px;
    border-radius: 8px;
    svg {
      width: 24px;
      height: 24px;
      path {
        fill: var(--svgColorMenu);
        stroke: var(--svgColorMenu);
      }
    }
    p {
      font-weight: 600;
      font-size: 16px;
      color: var(--menuColor);
      margin-left: 12px;
    }
  }
}

@media (max-width: 1100px) {
  .navItem {
    &:not(:first-child) {
      margin-top: 10px;
    }
  }
}
