@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.percentStatistics {
  @extend %flexCenter;
  width: 45px;
  height: 24px;
  background-color: $skyMainGreen;
  border-radius: 8px;
  font-weight: 600;
  font-size: 12px;
  color: $mainGreen;

  &.lower {
    background-color: $skyRed;
    color: $red;
  }
  &.empty {
    background-color: transparent;
  }
}
