@import "../../scss/repeat.scss";
@import "../../scss/vars.scss";

.filterBtn {
  @extend %flexCenter;
  width: 110px;
  height: 48px;
  border: 1px solid var(--borderColor);
  border-radius: 8px;
  cursor: pointer;

  &:not(:last-child) {
    margin-right: 10px;
  }

  &.active {
    background-color: $skyMainGreen;
    border-color: $mainGreen;
    svg path {
      fill: $mainGreen;
    }
    p {
      color: $mainGreen;
    }
  }
  svg path {
    fill: var(--menuColor);
  }
  p {
    font-weight: 600;
    font-size: 16px;
    color: var(--menuColor);
    margin-left: 10px;
  }
}

@media (max-width: 600px) {
  .filterBtn {
    width: 90px;
    height: 36px;
    p {
      font-size: 14px;
      line-height: 18px;
    }
  }
}
